.TimerView {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 15px;
  border-style: ridge;
}

.TimerView > h1 {
  flex: 1;
  margin: 1rem;
  text-align: left;
  font-variant-numeric: tabular-nums;
}

.TimerView > h2 {
  flex: 1;
}

.TimerView > button {
  padding: 0.25rem 0.5rem;
  margin-right: 5px;
  border-radius: 0.5rem;
  border: 1px solid #000;
  background-color: #000;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  width: 4rem;
}
